.Login-container{
    margin-top: 1vh;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 90vh;
    width: 100vw;
    
}
.message{
    height: 20vh !important;
    
    border-radius: 1rem;
    border: 2px solid var(--clr1);
    margin-top: 1vh;
}
.Login-form{
    display: flex;
    align-items: center;
    border-radius: 4rem;
    flex-direction: column;
    width: 35vw;
    height: 83vh; 
    background-color: var(--clr2);
    color: var(--clr1);
    border: 2px solid var(--clr1);

}
.signup{
    height: 105vh;
}
.signupform{
    height: 100vh;
}
.l{
    display: flex;
    flex-direction: column;
    height: auto;
    margin-top: 5vh;
    

}
.l img{
    width: 7vw;
    height: auto;
}
.l span{
    display: flex;
    flex-direction: row;
}
.l span h1{
    font-size: 2.5rem;
}
.shadow-drop-center {
	-webkit-animation: shadow-drop-center 0.2s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
	        animation: shadow-drop-center 0.2s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}
 @-webkit-keyframes shadow-drop-center {
    0% {
      -webkit-box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
              box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
    }
    100% {
      -webkit-box-shadow: 0 0 20px 0px rgba(0, 0, 0, 0.35);
              box-shadow: 0 0 20px 0px rgba(0, 0, 0, 0.35);
    }
  }
  @keyframes shadow-drop-center {
    0% {
      -webkit-box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
              box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
    }
    100% {
      -webkit-box-shadow: 0 0 20px 0px rgba(0, 0, 0, 0.35);
              box-shadow: 0 0 20px 0px rgba(0, 0, 0, 0.35);
    }
  }

.error{
    display: flex;
    align-items: center;
    justify-content: center;
    color: red;
    margin-top: 2vh;
    font-size: 1.3rem;
    
    visibility: hidden;
}

.email{
    display: flex;
    flex-direction: column;
    font-size: 2rem;
    margin-bottom: 3vh;
}
.email input{
    height: 2vh;
    border-radius: 1rem;
    
    border: 0.1px solid rgb(216, 215, 215);
    margin-top: 1vh;
    padding: 0.5rem;

   
}
.email input:focus{
    outline: none;
    border: 1px solid var(--clr1);
}

.email textarea{
    border: 0.1px solid rgb(216, 215, 215);
    padding: 0.5rem;
}
.email textarea:focus{
    
    outline: none;
    border: 1px solid var(--clr1);
}

.form-check{
    display: flex;
    align-items: center;
}
.form-check label{
    margin-left: 1vw;
}
.form-check input{
    width: 1.2rem;
    height: 1.2rem;
}
.btn{
    margin-top: 2vh;
    height: 7vh;
    width: 80%;
    cursor: pointer;
    font-size: 2em;
    border-radius: 5rem;
    border: none;
    color: var(--clr2);
    background-color: var(--clr4);
    box-shadow: -1px 1px 10px 2px rgba(250,132,96,0.59);
-webkit-box-shadow: -1px 1px 10px 2px rgba(250,132,96,0.59);
-moz-box-shadow: -1px 1px 10px 2px rgba(250,132,96,0.59);
font-family: 'Poppins', sans-serif !important;
letter-spacing: 2px;
justify-self: center;
}
.btn:hover{
    color: var(--clr2);
    background-color: var(--clr3);
    border: none;
    box-shadow: -1px 1px 10px 2px rgba(127,209,174,0.59);
-webkit-box-shadow: -1px 1px 10px 2px rgba(127,209,174,0.59);
-moz-box-shadow: -1px 1px 10px 2px rgba(127,209,174,0.59);
}




.Login-form form{
    margin-top: 5vh;
    display: grid;
}
.Login-form .form-control{
    width: 25vw;
    
}




.Loginbtn a:hover{
    color: var(--clr2);
}



