        width: 70vw;
        height: 3.5vh;
    }
    .btn{
        width: 80vw;
        height: 8vh;
    }
    .Loginbtn a{
        height: 7vw;
        width: 30vw;
    }
    .Loginbtn a img{
        width: 3vw;
    }
    .l span h1 {
        font-size: 1.5rem;
