@import url('https://fonts.googleapis.com/css2?family=Orbitron:wght@400;500;600;700&display=swap');

.accountcreated{
    position: absolute;
    top: 40vh;
    
    width: 100vw;
    
    display: flex;
    align-items: center;
    justify-content: center;
    
    z-index: 100;
    
   
    
    visibility: hidden;
    
}
.accountcreated div{
    width: 35vw;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 5vh;
    background-color: green;
    border-radius: 50rem;
    color: white;
    opacity: 0.9;
    font-size: 1.5rem;
    
    
    border-right:2px solid black;
}

.Login-container{
    margin-top: 1vh;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 90vh;
    width: 100vw;
    
}
.Login-form{
    display: flex;
    align-items: center;
    border-radius: 4rem;
    flex-direction: column;
    width: 35vw;
    height: 83vh; 
    background-color: var(--clr2);
    color: var(--clr1);
    border: 2px solid var(--clr1);

}
.signup{
    height: 105vh;
}
.signupform{
    height: 100vh;
}
.l{
    display: flex;
    flex-direction: column;
    height: auto;
    margin-top: 5vh;
    

}
.l img{
    width: 7vw;
    height: auto;
}
.l span{
    display: flex;
    flex-direction: row;
}
.l span h1{
    font-size: 2.5rem;
}
.shadow-drop-center {
	-webkit-animation: shadow-drop-center 0.2s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
	        animation: shadow-drop-center 0.2s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}
 @-webkit-keyframes shadow-drop-center {
    0% {
      -webkit-box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
              box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
    }
    100% {
      -webkit-box-shadow: 0 0 20px 0px rgba(0, 0, 0, 0.35);
              box-shadow: 0 0 20px 0px rgba(0, 0, 0, 0.35);
    }
  }
  @keyframes shadow-drop-center {
    0% {
      -webkit-box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
              box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
    }
    100% {
      -webkit-box-shadow: 0 0 20px 0px rgba(0, 0, 0, 0.35);
              box-shadow: 0 0 20px 0px rgba(0, 0, 0, 0.35);
    }
  }

.error{
    display: flex;
    align-items: center;
    justify-content: center;
    color: red;
    margin-top: 2vh;
    font-size: 1.3rem;
    
    visibility: hidden;
}

.email{
    display: flex;
    flex-direction: column;
    font-size: 2rem;
    margin-bottom: 3vh;
}
.email input{
    height: 4vh;
    border-radius: 1rem;
    border: 2px solid var(--clr1);
    margin-top: 1vh;
   
}
.form-check{
    display: flex;
    align-items: center;
}
.form-check label{
    margin-left: 1vw;
}
.form-check input{
    width: 1.2rem;
    height: 1.2rem;
}
.btn{
    margin-top: 2vh;
    height: 7vh;
    width: 100%;
    cursor: pointer;
    font-size: 2em;
    border-radius: 5rem;
}





.Login-form form{
    margin-top: 5vh;
}
.Login-form .form-control{
    width: 25vw;
}




.Loginbtn a:hover{
    color: var(--clr2);
}



