@import url('https://fonts.googleapis.com/css2?family=Play&display=swap');
.Navbar{
    
    height: 10vh; 
    width: 100%;  
    background-color: var(--clr2);
    display: flex;
    align-items: center;
    justify-content: space-between;
    
    
    border-bottom: 1px solid var(--clr1);
    /* position: fixed; */

}
.logo{
    margin-left: 2vw;
}
.logo a{
    text-decoration: none !important;
}
.lgo{
    width: 10vw;
    height: 6vh;
    text-decoration:none !important;
    display: flex;
    align-items: center;
    
}
.lgo h1{
    
    text-decoration:none !important;
    margin: 0;
    
    font-size: 1rem;
}
.BUDGET{
    color: var(--clr1);
}
.STORE{
    color: var(--clr3);
}
.lgo img{
    width: 5vw;
    height: 6vh;
    text-decoration:none !important;
    
}
a:-webkit-any-link{
    text-decoration:none !important;
    }
.search{
    margin-left: 2vw;
    display: flex;
    align-items: center;
    box-shadow: none;
    border-radius: 2px;
    width: 50vw;
    height: 5vh;
    
}
.search input{
    box-sizing: border-box;
    width: 56.5vw;
    height: 100%;
    background-color: #e9e9eb;
    border: 1px solid var(--clr1);
    box-sizing: border-box;
    
    vertical-align: top;
    color: var(--clr1);
    font-size: 1.2rem;
    padding: 0 1.2rem;
    border-radius: 3rem 0 0 3rem; 
    outline: none;
    border-right: none;
    
}
.search input:focus{
    border: 1px  solid var(--clr4);
    border-right: none;
}

.search button{
    width: 3.5vw;
    height: 100%;   
    border: none;
    background-color: var(--clr1);
    border: 1px solid var(--clr2);
    box-sizing: border-box;
    border-radius: 0 3rem 3rem 0; 
}
.scope{
    display: flex;
    justify-content: center;
    align-items: center;
}
.scopeicon{
    color: var(--clr2);
    width: 4vw;
    height: 4vh;
}
.cart{
    cursor: pointer;
    display: flex;
    align-items: center;
    
    color: var(--clr1);

}

.carticon:hover{
    color: var(--clr3);
    
}

.wrapper a{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 8.5vw;
    height: 7vh;
    font-size: 17px;
    
    text-decoration: none;
    color: var(--clr1) !important;
    border: 2px solid var(--clr1);
    letter-spacing: 2px;
    text-align: center;
    position: relative;
    transition: all .35s;
    cursor: pointer;
    
    
  }
  
  .wrapper a span{
    position: relative;
    z-index: 2;
  }
  
  .wrapper  a:after{
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    width: 0;
    height: 100%;
    background: var(--clr1 ) !important;
    transition: all .35s;
    
  }
  
  .wrapper a:hover{
    color: var(--clr2) !important;
    
  }
  
  .wrapper a:hover:after{
    width: 100%;
    
  }
  .cart1{
    position: fixed;
    z-index: 6;
    top: 0;
    right: 0;
    width: 25vw;
    height: 100vh;
    transition: 0.4s;
  }
  .right-nav{
    display: flex;
    align-items: center;
    column-gap: 2vw;
    margin-right: 2vw;
  }



  .hamburgermenu{
    width: 100%;
    display: inline-flex;
    position: fixed;
    top: min(100vh - 100px, 0.5vh);
    height: 100vh;
    overflow-x: hidden;
    z-index: 7;
    float: right;
    right: 0px;
    
}
.hamburger{
    cursor: pointer;
    margin-left: 1vw;
    display: none;
    

}
  @media (max-width:600px)  {
    .hamburger{
        
        display: block;
        font-size: 3rem;
    }
    .Navbar{
        height: 8vh;
    }
    .logo img{
        height: 2vh;
    }
    .search{
        /* margin-left: 0.1vw; */
        border-radius: 2px;
        width: 50vw;
        height: 3vh;
        
    }
    .search button{
        width: 4vw;
        height: 2.8vh;   
    
    }
    .cart{
        
        /* margin-left: 2vw; */
        
        font-size: 3rem;
    
    }
    
    /* .cart img{
        width: 4vw;
        height: 4vw;
    } */
    .wrapper{
        display: none;
    }
    .wrapper a{
        display: flex;
        justify-content: center;
        align-items: center;
        width: 11vw;
        height: 4vh;
        font-size: 6px;

        letter-spacing: 2px;
      }
      .wrapper a:hover{
        color: var(--clr1) !important;
        border-radius: 1rem 0 1rem 0;
      }
      .cart1{
        
        width: 100vw;
        z-index: 10;
        
      }
      .carticon{
        display: flex;
    
    }
}
@media (min-width:600px) and (max-width: 1000px) {
    .cart{
        width: 8vw;
        height: 4vh;
    
    }
    .carticon{
        width: 8vw;
        height: 4vh;
    
    }
    .wrapper a{
        width: 18vw;
        font-size: 1.2rem;
        
        
        border: 1px solid var(--clr1);
        
        
        
      }
    

}



@media (min-width: 1000px) and (max-width: 1300px)  {
    .wrapper a{
        
        font-size: 0.8rem;
        
        
        border: 1px solid var(--clr1);
        
        
        
      }
}
@media (min-width: 1300px) and (max-width: 1600px)  {
    .wrapper a{
        
        font-size: 0.9rem;
        
        
        border: 1.5px solid var(--clr1);
        
        
        
      }
}
@media (min-width: 2560px) and (max-width: 3839px)  {
    .wrapper a{
        
        font-size: 1.8rem;
        
        
        border: 3px solid var(--clr1);
        
        
        
      }
      .lgo h1{
    
        font-size: 2rem;
    }
    .search input{
        font-size: 1.5rem;
        color: var(--clr1);
        padding: 1.2vw;
        
    }
    
}
@media (min-width: 3840px) {
    .wrapper a{
        
        font-size: 2.8rem;
        
        
        border: 4px solid var(--clr1);
        
        
        
      }
      .lgo h1{
    
        font-size: 3rem;
    }
    .search input{
        font-size: 2rem;
        color: var(--clr1);
        padding: 1vw;
        
    }
}
