@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@200;400&display=swap');
.cartmenu{
    width: 25vw;
    height: 100vh;
    border: 1px solid black;
    overflow: hidden;
    position: sticky;
    z-index: 60;
    float: right;
    background-color: var(--clr2);
    

    
}
.topheading{
    width: 25vw;
    background-color: var(--clr1);
    
    height: 9%;
    display: flex;
    align-items: center;
    
   
}
.topheading a{
    display: flex;
    align-items: center;
    color: var(--clr2);
    cursor: pointer;

}
.topheading h3{
    font-size: 1.4vw;
    
    font-weight: 700;
    letter-spacing: 2px;
    text-align: center;
    margin: 0;
    
}
.topheading img{
    width: 1.5vw;
    transform: rotate(90deg);
    
}
.emptycart{
    display: flex;
flex-direction: column;
align-items: center;
}
.mid{
    width: 25vw;
    position: relative;
    background-color: var(--clr2);
    display: flex;
    align-items: center;
    flex-direction: column;
    height: 63%;
    overflow-y: scroll;
    overflow-x: hidden;
    
}
.mid h3{
    color: var(--clr1);
    
    font-weight: 700;
    letter-spacing: 2px;
}
.para{
    display: flex;
    
    font-weight: 300;
    font-size: 0.8rem;
}
.para a{
    margin: 0;
    color: var(--clr3);
    cursor: pointer;
}
.para p{
    margin-top: 0;
    width: 22vw;
    text-align: center;
    letter-spacing: 2px;
    color: var(--clr1);
}
.mid-para{
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: space-between;
    height: 45vh;
    

}
.payment{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.payment p{
    margin: 0;
    font-size: 0.6rem;
    letter-spacing: 2px;
    color: var(--clr1);
}
.payment-img img{
    width: 2.3vw;
    margin: 0 0.6vw;
    margin-top: 1vh;

}
.bottomheading{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    float: inline-end;
    height: 28%;
    background-color: var(--clr1);
    width: 25vw;
    
    font-weight: 700;
    flex: 1;
}
.alltotal{
    display: flex;
    flex-direction: column;
    height: 27vh;
    justify-content: space-around;
    width: 90%;
}
.total{
    display: flex;
    color: var(--clr2);
    justify-content: space-between;
    margin: 1vh 1.5vw;
    width: 100%;
    margin: 0;
}
.total h4{
    font-weight: 700;
    letter-spacing: 2px;
    margin: 4px 0;
}
.chckbtn{
    
    background-color: var(--clr4);
    box-shadow: -1px 1px 10px 2px rgba(250,132,96,0.59);
-webkit-box-shadow: -1px 1px 10px 2px rgba(250,132,96,0.59);
-moz-box-shadow: -1px 1px 10px 2px rgba(250,132,96,0.59);
    color: var(--clr2);
    cursor: pointer;
    border: none;
    width: 100%;
    height: 7vh;
    font-size: 1.5rem;
    border-radius: 1rem;
    letter-spacing: 1.5px;
    text-decoration: none;
    display: flex;
    justify-content: center;
    align-items: center;

}
.code{
    color: #fead94;
    font-size: 0.7rem;
    letter-spacing: 3px;
    margin-top: 3vh;
}
.cartproducts{
    width: 100%;
    display: flex;
    margin-left: 2vw;
    margin-top: 2vw;
}
.cartimg{
    width: 7vw;
    margin: 0;
    
}
.cartimg img{
    width: 6vw;
    height: 6vw;
    margin: 0;
    border-radius: 1rem;
}
.carttxt{
    margin-left: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    row-gap: 1vh;
}

.carttxt h6{
    margin: 0;
    font-size: 1.2rem;
    color: var(--clr1);

}
.carttxt p{
    margin: 0;
    font-size: 1.5rem;
    color: var(--clr1);
}
.qty{
    display: flex;
    align-items: center;
    color: var(--clr1);
    
}
.qty button{
    height: 1.4vw;
    width: 1.4vw;
    border: none;
    margin-left: 1vw;
    color: var(--clr2);
    background-color: var(--clr4);
    cursor: pointer;
    font-family: 'Poppins', sans-serif;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 0.5rem;
    box-shadow: -1px 1px 10px 2px rgba(250,132,96,0.59);
-webkit-box-shadow: -1px 1px 10px 2px rgba(250,132,96,0.59);
-moz-box-shadow: -1px 1px 10px 2px rgba(250,132,96,0.59);
}
.qty button:hover{
    color: var(--clr1);
    background-color: var(--clr3);
    box-shadow: -1px 1px 10px 2px rgba(127,209,174,0.59);
-webkit-box-shadow: -1px 1px 10px 2px rgba(127,209,174,0.59);
-moz-box-shadow: -1px 1px 10px 2px rgba(127,209,174,0.59);
}
.nxtprevbtn{
    position: absolute; 
    bottom: 0;
    width: 100%;
    display: flex;
    /* justify-content: flex-end; */
   
    margin-bottom: 1.5vh;
    height: 5vh;

}
.nxt-btn{
    margin-top: 1vh;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 3vh;
    width: 7vw;
    font-size: 0.8rem;
    color: var(--clr2);
    background-color: var(--clr1);
    border-radius: 50rem !important;
    padding: 0;
    cursor: pointer;
    border: none;
    position: absolute;
    right: 0;
    bottom: 0;
    margin-right: 1vw;
   
}
.prev{
    position: absolute;
    left: 0;
    margin-left: 1vw;
}
.nxt-btn:hover{
    color: var(--clr1);
    background-color: var(--clr3);
}
