@media (max-width:600px)  {
    .cartmenu{
        width: 100vw;
    }
    .topheading{
        width: auto;
    }
    .topheading h3{
        font-size: 1rem;
    }
    .mid{
        width: auto;
        height: 63vh;
    }
    .para p{
        margin-top: 0;
        width: 80vw;}
    .bottomheading{
        width: 100vw;
        /* justify-content: space-around; */
    }
    .total h4{
        margin-top: 1vh;
    }
    .chckbtn{
        margin-top: 1vh;
        margin-bottom: 2vh;
    }
    .payment-img img{
        width: 7vw;
    }
    .cartproducts{
    margin-left: 4vw;
    margin-top: 2vh;
}
.cartimg{
    width: 32vw;
    margin: 0;
}
.cartimg img{
    width: 32vw;
    height: 32vw;
    margin: 0;
}
.carttxt{
    height: 32vw;
    margin-left: 2vw;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.carttxt h6{

    font-size: 1rem;

}
.carttxt p{
    font-size: 1.5em;
}
.qty button{
    height: 2rem;
    width: 2rem;
    margin-left: 1rem;
}
.nxt-btn{
    height: 3rem;
    width: 7rem;
    font-size: 1.5rem;
}
}
