.Alert{

    width: 35vw;
    height: 5vh;
    background-color: red;
    border-radius: 50rem;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    
    font-size: 1.5rem;
    z-index: 20;
    opacity: 0.7;
    border-right:2px solid black;
    border-bottom:2px solid black
    
}
@media (max-width:801px)  {
    .Alert{

        width: 80vw;
        font-size: 1rem;
        
    }
}