@import url('https://fonts.googleapis.com/css2?family=PT+Sans+Narrow:wght@400;700&family=Play&display=swap');

.row-1{
    margin-top: 4vh;
    display: flex;
    justify-content: space-around;
    flex-direction: row;
    margin-bottom: 3vh;
}

.column{
    margin: 0 3vw;
    display: flex;
    flex-direction: column;
   
}

.column a{
    text-decoration: none;
    color: var(--clr1) !important;
    
    cursor: pointer;
    
}
.column a:hover{
    color: var(--clr3) !important;

}
.Bottom{
    margin-bottom: 3vh;
    
}
.Bottom p{
    font-size: 0.7rem;
    margin-left: 2vw;
    
    
}

@media (max-width:600px)  {

    .row-1{
        display: block;
    }
    .Bottom{
        width: 95%;
    }
    .Bottom p{
        font-size: 0.6rem;
        
        
    }
}
@media (min-width: 1000px) and (max-width: 1300px)  {
    .Bottom p{
        font-size: 0.7rem;
        margin-left: 2vw;
        
        
    }
    .column h4{
        font-size: 0.9rem;
    }
}
@media (min-width: 2560px) and (max-width: 3839px)  {
    .Bottom a{
        font-size: 1.4rem ;
        margin-left: 2vw;
        
        
    }
    .column h4{
        font-size: 1.8rem;
    }
}
@media (min-width: 3840px) {
    .Bottom p{
        font-size: 2rem;
        margin-left: 2vw;
        
        
    }
    .Bottom a{
        font-size: 1.9rem ;
        margin-left: 2vw;
        
        
    }
    .column h4{
        font-size: 3rem;
    }
}