@import url('https://fonts.googleapis.com/css2?family=PT+Sans+Narrow:wght@400;700&family=Play&display=swap');

.coupon-container{
  display: flex;
  width: 95vw;
  height: 16vh;
  background-color: var(--clr1);
  padding-right: var(--bs-gutter-x,.75rem);
  padding-left: var(--bs-gutter-x,.75rem);
  margin-right: auto;
  margin-left: auto;
  /* width: 100%; */
  
  text-decoration: none;
  color: white;
  align-items: center;
  overflow: hidden;
  border-radius: 2rem;
  box-sizing: border-box;
}

.text{
  color: var(--clr3);
  position: relative;
  top: 5px;
  /* line-height: 0.6; */
  
  width: 70vw;
  
  
}
.coupon-container h4{
  font-weight: bold;
  background-color: var(--clr1);
  font-size: 2.2vw;
  margin: 0;
  
  font-weight: bold;

}
