.coupon-btn{
  display: flex;

  justify-content: flex-end;
  align-items: center;
  height: 5vw;
  width: 100%;
  margin-left: 15vw;

 

}
.coupon-btn a{
  border: 2px solid var(--clr3);
  border-radius: 1rem 0 1rem 0;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 20vw;
  font-size: 1.5vw;
  font-weight: bold; 
  
  text-decoration: none;
  color: var(--clr3);
 
  letter-spacing: 2px;
  text-align: center;
  position: relative;
  transition: all .35s;
  
  font-weight: bold;
}

.coupon-btn a span{
  position: relative;
  z-index: 2;
}

.coupon-btn  a:after{
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 0;
  height: 100%;
  background: var(--clr2);
  transition: all .35s;
  border-radius: 1rem 0 1rem 0;
}

.coupon-btn a:hover{
  color: var(--clr1);
}

.coupon-btn a:hover:after{
  width: 100%;
}
