.ForYou-container{
    align-items: center;
    margin-left: auto;
}

.Big-text{
    margin-left: 1vw;
    color: var(--clr1);
    font-size: 3vw;
    font-weight: bolder;
    text-decoration: none;
    text-align: center;
    margin-bottom: 1vh;
    
}
.products-row{
    display: grid;
    grid-template-rows: repeat(1,1fr);
    grid-template-columns: 15vw 15vw 15vw 15vw 15vw;
    column-gap: 1vw;
    justify-content: center;
}
.Products{
    height: 55vh;
    width: 20vw;
    
}
/* .Products:first-child{
    margin-left: 2vw;
    
} */
.loadbtndiv{
    /* width: 100vw; */
    display: flex;
    justify-content: center;
}
.Load-btn{
    
    margin-top: 2vh;
    margin-bottom: 1vh;
    font-size: 1.5vw;
    font-weight: bold;
    height: 5vw;
    width: 13vw;
    color: var(--clr1);
    background-color: var(--clr2);
    border: 1px solid var(--clr1) !important;
    transition: ease-in-out;
    transition-duration: 0.1s;
    
    transform: skew(25deg);
    cursor: pointer;
    
}
.Load-btn p{
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    transform: skew(-25deg)
}
.Load-btn:hover {
    background-color: var(--clr1);
    color: var(--clr2);
    border: 1px solid var(--clr2);

  }

  @media (max-width:600px)  {
    .products-row{
        justify-content: space-around;
        grid-template-rows: repeat(1,1fr);
        grid-template-columns: 30vw 30vw;
        column-gap: 0vw;
        row-gap: 2vh;
    }
    .Products{
        height: 25vh;
        width: 30vw;
        display: flex;
        align-items: center;
        flex-direction: column;
        flex-wrap: wrap;
    }

    .Load-btn{
        
        height: 5vh;
        width: 30vw;
        font-size: 3vw;
    }
  }
  @media (min-width:600px) and (max-width: 1000px) {
    .products-row{
        justify-content: space-around;
        grid-template-rows: repeat(1,1fr);
        grid-template-columns: 30vw 30vw 30vw;
        column-gap: 0vw;
        row-gap: 2vh;
    }
    .Products{
        height: 25vh;
        width: 30vw;
        display: flex;
        align-items: center;
        flex-direction: column;
        flex-wrap: wrap;
    }

    .Load-btn{
        
        height: 7vh;
        width: 30vw;
        font-size: 3vw;
    }
  }