.container1{
  margin-top: 5vh;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 100%;
  height: 70vh;
  
}
.container1 img{
  width: 60rem;  
  /* 988px */
  height: 50vh;

  border-radius:  0 5rem 0 5rem ;

}
.grey-btn{
display: flex;
justify-content: center;
}
.swiper-pagination-bullet{
  
  background-color: var(--clr2) !important;
}
.container1 .btn-circle {
  position: relative;
  width: 1vw;
  height: 1.5vh;
  background-color: #dddddd;
  margin-left: 1vw;
  border-radius: 50rem;
  border: 1px solid transparent;
  bottom: 3.337041156840934vh;
  
}

.container1 .btn-circle:hover {
  background-color: white;
}

.swiper {
  width: 80vw; 
  height: 100%;
}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;

  /* Center slide text vertically */
  display: flex;
  justify-content: center;
  align-items: center;
}

.swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

