  @media (max-width:600px)  {
    .container1{
      
      height: 20vh;
      
    }
    .swiper-slide img {
      
      width: 100%;
      height: 20vh;
      
      
    }
    .container1 img {
      
      border-radius: 0 1.5rem 0 1.5rem;
    }
    .container1 {
      margin-top: 2vh;
      margin-bottom: 2vh;
      
    }
  }
  @media (min-width:600px) and (max-width: 1000px) {
    .container1{
      
