@import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300;400;700&display=swap');
:root{
    --brand-multiply: 1
}
.Brand-container{
    margin-top: 1vh;
    display: flex;
    margin-right: auto;
    margin-left: auto;
    position: relative;
    
    flex-direction: column;
    height: 100vh;
    
}
.heading{
    color: var(--clr1);
    display: flex;
    margin-left: 1.5vw;
    align-items: center;
    text-align: center;
    clear: both;
    
    
}
.Big-text{
    color: var(--clr1);
    font-size: 3vw;
    font-weight: bolder;
    text-decoration: none;
    text-align: center;
    
}

.Brands{
    
    align-self: center;
    margin-left: 3rem;
    display: grid;
    grid-template-columns: 25vw 25vw 25vw;
    grid-template-rows: repeat(2, 40vh);
    column-gap: 1vw;
    row-gap: 1vw;
    overflow: hidden;
}
/* .BrandsItem{
    height: 80vh;
} */
.BrandsItem:first-child{
    grid-row: 1 / span 2;
}


.BrandsItem:nth-child(4){
    grid-column: 2 / span 2;
}
.BrandsItem div div{
    position: relative;
    overflow: hidden;
}
.BrandsItem div div p{
    position: absolute;
    z-index: 4;
    color: var(--clr2);
    font-size: 3em;
    bottom: 3vh;
    margin-left: 2vw;

    
}
.BrandsItem div div img{
    filter: brightness(70%);
    transition: .2s;
    
}
.BrandsItem div div img:hover{
    filter: brightness(100%);
    transform: scale(1.1);
}

.BrandsItem:first-child div div{
    
        
        border-radius: 2rem 0 0 2rem;
        
    
}
.BrandsItem:first-child div div img{
    
        width: 25vw;
        height: 82vh;
        
        
    
}
.BrandsItem:nth-child(2) div div img{
    
        width: 25vw;
        height: 40vh;
    
}
.BrandsItem:nth-child(3) div div{
    
        
        border-radius: 0 2rem 0 0;
    
}
.BrandsItem:nth-child(3) div div img{
    
        width: 25vw;
        height: 40vh;
       
    
}
.BrandsItem:nth-child(4) div div{

        border-radius: 0 0 2rem 0;
    
    
}
.BrandsItem:nth-child(4) div div img{
    
    
        width: 51vw;
        height: 40vh;
        
    
    
}
@media (max-width:600px)  {
        .Brand-container{
                
                height: 35vh;
                margin: 5vh 0;
                
            }
        .BrandsItem:first-child div div{
    
        
                border-radius: 1rem 0 0 1rem;
                
            
        }
            .BrandsItem div div p{
            
                font-size: 1rem;
                
                
            
                
            }
            .Brands{
                
                margin-left: 0;
                grid-template-columns: 28vw 28vw 28vw;
                grid-template-rows: repeat(2, 15vh);
                
            }
            .BrandsItem:first-child div div img{
                
                    width: 28vw;
                    height: 31vh;
                    
                    
                
            }
            .BrandsItem:nth-child(2) div div img{
                
                    width: 28vw;
                    height: 15vh;
                
            }
            .BrandsItem:nth-child(3) div div{
                
                    
                    border-radius: 0 1rem 0 0;
                
            }
            .BrandsItem:nth-child(3) div div img{
                
                    width: 28vw;
                    height: 15vh;
                   
                
            }
            .BrandsItem:nth-child(4) div div{
            
                    border-radius: 0 0 1rem 0;
                
                
            }
            .BrandsItem:nth-child(4) div div img{
                
                
                    width: 57vw;
                    height: 15vh;
                    
                
                
            }
}
