@import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300;400;700&display=swap');
.Topbar{
    z-index: 10;
    display: flex;
    justify-content: center;
    background-color: var(--clr2) ;
    border-bottom: 1px solid var(--clr1);
    width: 100%;
    height: 4vh;
    align-items: center;
    
}
dl, ol, ul {
    display: flex;
    align-items: center;
    }


.topbaritem{
    list-style: none;
    display: inline-block;
    line-height: 20px;
    margin-left: 20px;
    cursor: pointer;
    font-weight: 600;

    
    
    

}
.topbaritem a{
    text-decoration: none;
   color: var(--clr1) !important;
    transition: color .2s;
    
}
.topbaritem a:hover{
    color: var(--clr3) !important;
    
}
@media (max-width:600px)  {
    .Topbar{
        width: 100%;
        height: 10vh;
        display: none;
    }
    dl, ol, ul {
        display: inline-block;

        }
    .topbaritem a{
        
        padding: 2px;
        border: var(--clr1) solid 1px;
    }
    .topbaritem:first-child {
        margin-left: 0;
     }
    .topbaritem{
        line-height: 20px;
        
    }
    .row-3{

        display: flex;
        justify-content: center;
        /* margin-top: 2vh; */
    }
    
}

@media (max-width:801px)  {
    .Topbar{
        width: 100%;
        height: 7vh;
    }

    .topbaritem a{
        
        padding: 5px;
        border: var(--clr1) solid 1px;
    }
    .topbaritem:first-child {
        margin-left: 0;
     }
    .topbaritem{
        line-height: 20px;
        
    }
    .row-3{

        display: flex;
        justify-content: center;
        margin-top: 2vh;
    }
}
@media (min-width: 2560px) and (max-width: 3839px)  {
    .Topbar{
        
        height: 5vh;
        
        
    }
    .topbaritem{
        font-size: 2rem;
        letter-spacing: 2px;
        
        
        
    
    }
    .topbaritem a{
        margin-right: 1vw;
        
    }
}
@media (min-width: 3840px) {
    .Topbar{
        
        height: 6vh;
        
        
    }
    .topbaritem{
        font-size: 3rem;
        letter-spacing: 2px;
    
        
        
        
    
    }
    .topbaritem a{
        margin-right: 2vw;
        
    }
}