.hammenu{
    /* margin-top: 50vh; */
    display: inline-flex;
    position: sticky;
    /* float: right; */
    height: 100%;
    z-index: 10;
    background-color:  var(--clr1);;
    width: 100%;
    flex-direction: column;
    /* margin-right: 0; */
    /* align-items: center; */
    
    
    
    
}
.menu{
    width: 100%;
    display: flex;
    flex-direction: column;
    row-gap: 3vh;
    /* margin-left: 3vw; */
    align-items: center;
}
.menuitem{
    list-style: none;
}
.cancelbtn{
    width: 100%;
    margin-top: 1vh;
    margin-bottom: 3vh;
    margin-left: 1vw;
    color: var(--clr2);
    
}
.cancelbtn:first-child{
    cursor: pointer;
}
.menuitem:first-child{
    margin-top: 5vh;
}
.menuitem a{
    text-decoration: none !important;
    cursor: pointer;
    display: flex;
    color: var(--clr2);
    font-size: 2rem;
    font-family: 'Poppins', sans-serif !important;
    background-color: var(--clr4);
    width: 80vw;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 2rem;
    height: 9vh;
    box-shadow: -1px 1px 10px 2px rgba(250,132,96,0.59);
-webkit-box-shadow: -1px 1px 10px 2px rgba(250,132,96,0.59);
-moz-box-shadow: -1px 1px 10px 2px rgba(250,132,96,0.59);
}
.menuitem a:hover{
    color: var(--clr2);

}

.visible{
    display: flex;
    margin-top: 2vh;
    
       


}