@import url('https://fonts.googleapis.com/css2?family=Space+Grotesk:wght@500&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Play&display=swap');
.banner{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 30vh;
    width: 100%;

    background-color: var(--clr2);
    color: var(--clr1);
}
.main-grid{
    display: flex;
    margin-top: 1vw;
    

}
.banner h1{
    font-size: 7vw;
    
    
}
.banner h4{

    
    
}
.products-categories{
    cursor: pointer;
    
    width: fit-content;

}
.products-categories:hover{
    
    color: var(--clr2);

}

.products-categories h4{
    color: var(--clr1);
    margin-left: 1.5vw;
    font-weight: 600;
    
}
.left-grid{
    width: 25vw;
    margin-left: 2vw;
    
}
.left-categories{
    color: var(--clr1);
    border: 1px var(--clr1) solid;
    border-radius: 2rem;
    padding: 0 1vw;
}
.left-grid h1{
    font-size: 2rem;
    font-weight: bold;
    
    font-weight: bolder;

}
.right-grid{
    display: flex;
}
.Products-1{
    width: 23%;
}
@media screen and (min-aspect-ratio: 16/9) {
    .banner{
        height: 55vh;
    }
    .banner h1{
        font-size: 12rem;
        
    }
    .banner h4{
        font-size: 4rem;
    }
    .main-grid{
        width: 100vw;
        flex-direction: column;
        align-items: center;
        margin-bottom: 2vh;
    }
    .left-grid{
        
        width: 95vw;
        margin-left: 2vw;
        margin-bottom: 2vh;
        margin-top: 2vh;
        border-radius: 0;
        height: 24vh;

        
    }
    .left-categories{
        height: 23vh;
    }
    .left-grid h1{
        font-size: 5rem;
        width: 90vw;
        display: flex;
        justify-content: center;
    }

    .procat{
        display: grid;
        grid-template-rows: repeat(5vh,1fr);
        grid-template-columns: 30vw 30vw;
        column-gap: 10vw;
        row-gap: 2vh;
        flex-wrap: wrap;
        justify-content: space-around;
        
      }

    .products-categories{
        font-size: 3rem;
        display: flex;
        flex-direction: row;
       
        
    }
}
@media (max-width:801px)  {
    .banner{
        height: 30vh;
    }
    .banner h1{
        font-size: 2.5rem;
        
    }
    .banner h4{
        font-size: 1.5rem;
    }
    .main-grid{
        width: 100vw;
        flex-direction: column;
        align-items: center;
        margin-bottom: 2vh;
    }
    .left-grid{
        
        width: 90vw;
        margin-left: 2vw;
        margin-bottom: 2vh;
        margin-top: 2vh;


        
    }
    .left-grid h1{
        font-size: 2rem;
        width: 90vw;
        display: flex;
        justify-content: center;
    }

    .procat{
        display: grid;
        grid-template-rows: repeat(5vh,1fr);
        grid-template-columns: 30vw 30vw;
        column-gap: 10vw;
        row-gap: 2vh;
        flex-wrap: wrap;
        justify-content: space-around;
      }

    .products-categories{
        font-size: 1.5rem;
        display: flex;
        flex-direction: row;
        
    }
    .right-grid{
        width: 70vw;
    }
}
