.confirm{
    height: 80vh;
    display: flex;
    flex-direction: column;
    align-items: center;

}
.chck{
    color: rgb(167, 238, 167);
    width: 15rem;
    height: 15rem;
}
.confirm h1{
    color: rgb(167, 238, 167);
    font-size: 4rem;
}
.time{
    width: 40vw;
    display: flex;
    justify-content: space-between;
    color: rgb(109, 109, 109);
    font-size: 2.5rem;

}
.time div{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.time h3{
    color: rgb(167, 238, 167);
    font-size: 2rem;
}
.continueshopping{
    margin-top: 2vh;
    text-decoration: none;
    justify-self: flex-end;
    background-color: var(--clr4);
    box-shadow: -1px 1px 10px 2px rgba(250,132,96,0.59);
-webkit-box-shadow: -1px 1px 10px 2px rgba(250,132,96,0.59);
-moz-box-shadow: -1px 1px 10px 2px rgba(250,132,96,0.59);
    color: var(--clr2);
    cursor: pointer;
    border: none;
    width: 20vw;
    height: 7vh;
    font-size: 1.5rem;
    border-radius: 1rem;
    letter-spacing: 1.5px;
    display: flex;
    justify-content: center;
    align-items: center;
}