@import url('https://fonts.googleapis.com/css2?family=PT+Sans+Narrow:wght@400;700&family=Play&display=swap');
.tenbg{
    width: 8vw;
    height: 8vw;
    margin-right: 0.5vw;
    border-radius: 20%;
    background-color: var(--clr2);
    color: var(--clr1);
    display: flex;
    justify-content: center;
    align-items: center;
    
}
.dllr{
    font-size: 2rem;
    font-weight: 900;
    
}
.ten{
    font-size: 3rem;
    font-weight: 700;
    
    
}
@media screen and (min-aspect-ratio: 16/9) {
    .dllr{
        font-size: 3rem;
        font-weight: 900;
    }
    .ten{
        font-size: 4rem;
        font-weight: 700;
    }
}
@media (max-width:801px)  {
    .tenbg{
        width: 12vw;
        height: 12vw;
    }
    .dllr{
        font-size: 1rem;
    }
    .ten{
        font-size: 1.5rem;

    }
}